							</div>
						</div>
					</div>
				</div>
				<div class="detail-more-list-item">
					<div class="detail-more-list-top">
						<img src="../../static/img/icon/denglu_gengxin_icon/logo_denglu@2x.png"/>
					</div>
					<div class="detail-more-list-bottom">
						<div class="detail-more-list-title">
							萨达所大所大所大所多
						</div>
						<div class="detail-more-list-money">
							<div>
								￥<span>12.00</span>
							</div>
							<div>
								已售55
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>-->
		<div style="width: 100%;height: 60px;"></div>
		<div class="detail-purchase" v-if="shopShow || !commodityStatus">
			<div class="detail-purchase-icon" @click="route('customerCenter')">
				<img src="../../static/img/icon/find_icon/kefu@2x.png"/>
				<div>客服</div>
			</div>
			<div class="detail-purchase-icon1" @click="route('shoppingCart')">
				<img src="../../static/img/icon/find_icon/shoppingcart-faxian@2x.png"/>
				<div class="red-num">{{shoppingNum}}</div>
			</div>
			<div class="detail-purchase-icon2">
				<img src="../../static/img/icon/find_icon/shouchan@2x.png"/>
