			<div  @click="show = true">
				立即购买
			</div>
		</div>
		<transition name="slideup">
			<purchase-mask :diolog="show" :url="imgs" :list="attribute" @hide="hideMask" @addShopping="addShopping" @shop="shop"></purchase-mask>			
		</transition>
		<v-share :dialog="shareShow" @hide="hide"></v-share>
		<div class="detail-fixed" @click="scrollTop">
			<div>返回顶部</div>
		</div>
