</template>

<script>
	import purchaseMask from '../base/purchase'
	import {getData} from '../service/getData'
	import VShare from '../base/v-share'
	export default {
		components: {
			purchaseMask,
			VShare
		},
		data() {
			return {
				index:['商品详情','销量和评价'],
				tabId: 0,
				show: false,
				shopShow: false,
				shoppingNum: 0,
				index2: [{
					name: '全部',
					evaluateList: [],
					num: 0
				},{
					name: '好评',
					evaluateList:[],
					num: 0
				},{
					name: '中评',
					evaluateList: [],
					num: 0
				},{
					name: '差评',
					evaluateList : [],
					num: 0
				}],
				tabIndex: 0,
				info: {},
				attribute: [],
				imgs: '',
				firstImg: '',
				lastImg: '',
				commodityStatus: true,
				commonCount: {},
				shareShow: false
			}
		},
		methods: {
			tabItem(index) {
				this.tabId = index
			},
			tabItem1(index) {
				this.tabIndex = index
			},
			hideMask() {
				this.show = false
			},
			hide() {
				this.shareShow = false
			},
			addShopping(info) {
//				this.shoppingNum += 1
				getData('/shoppingtrolley/addCommodityOnShoppingApi', {
					userId: sessionStorage.userId,
					commodityId: this.$route.params.id,
					attributeId: info.attributeId,
					sortId: info.CommoditySortID,
					buyCount: info.num
				}).then(d => {
					if(d.code = 1) {
						this.shoppingNum ++						
					} else {						
						alert(d.msg)
					}
				})
			},
			scrollTop() {
//				var timer = setInterval(() => {
//					window.scrollTo(0,$('.Router').scrollTop()-50);	
//					if($('.Router').scrollTop() ==0) {
//						clearInterval(timer)
//					}
//				},50)
				$('.Router').scrollTop(0)
			},
			init() {
				getData('/commodity/getCommodityByIdApi', {
					userId: sessionStorage.userId,
					CommodityID: this.$route.params.id
				}).then(d => {
					var arr = []
					var newArr = []
					arr = d.commodity.titleUrl.split(',')
					arr.map(x => {
						if(x != '') {
							newArr.push(x)
						}
					})
					this.info = d.commodity
					this.info.titleUrl = newArr
					this.shoppingNum = d.shoppingCount
					this.imgs = newArr[0]
					this.commodityStatus = d.commodity.commodityStatus == 1 ? true : false
					this.shopShow = d.collectStatus == 0 ? true : false
					this.firstImg = this.info.titleUrl[0]
					this.lastImg = this.info.titleUrl[this.info.titleUrl.length-1]
					this.index2[0].num = d.evaluateCount.allCount
					this.index2[1].num = d.evaluateCount.haoPingCount
					this.index2[2].num = d.evaluateCount.zhongPingCount
					this.index2[3].num = d.evaluateCount.chaPingCount
					this.$nextTick(() => {
					    //获得slider插件对象
//					    mui(".mui-slider").slider();
					    var slider = mui(".mui-slider");
						slider.slider({
							interval: 5000
						});
					})
				})
				
				getData('/commodityattribute/getAttributeByCommodityIdApi', {
					commodityId: this.$route.params.id
				}).then(d => {
					this.attribute = d.sortList
				})
			},
			shop(info) {
				getData('/shoppingtrolley/determineInventoryApi', {
					buyCount: info.num,
					userId: sessionStorage.userId,
					CommodityID: this.$route.params.id,
					CommoditySortID: info.CommoditySortID
				}).then(d => {
					if(d.code == 1) {
						info.id = this.$route.params.id
						this.route('order','','shop',JSON.stringify(info))
					} else {
						alert('库存不够或者购买上限')
					}
				})
			}
		},
		mounted() {
			this.init()
			this.$nextTick(() => {
			    //获得slider插件对象
			    mui(".mui-slider").slider();
			    var slider = mui(".mui-slider");
				slider.slider({
					interval: 1000
				});
			})
		},
		watch: {
			tabId(val) {
				if(val == 1) {
					getData('/evaluate/getEvaluateByCommodityIdApi', {
						pn: 1,
						commodityId: this.$route.params.id,
						scoreType: 0
					}).then(d => {
						this.index2[0].evaluateList = d.pageInfo.list
					})
				}
			},
			tabIndex(index) {
				if(index == 0) {
					getData('/evaluate/getEvaluateByCommodityIdApi', {
						pn: 1,
						commodityId: this.$route.params.id,
						scoreType: 0
					}).then(d => {
						this.index2[0].evaluateList = d.pageInfo.list
					})
				} else if(index == 1) {
					getData('/evaluate/getEvaluateByCommodityIdApi', {
						pn: 1,
						commodityId: this.$route.params.id,
						scoreType: 1
					}).then(d => {
						this.index2[1].evaluateList = d.pageInfo.list
					})
				} else if(index == 2) {
					getData('/evaluate/getEvaluateByCommodityIdApi', {
						pn: 1,
						commodityId: this.$route.params.id,
						scoreType: 2
					}).then(d => {
						this.index2[2].evaluateList = d.pageInfo.list
					})
				} else if(index == 3) {
					getData('/evaluate/getEvaluateByCommodityIdApi', {
						pn: 1,
						commodityId: this.$route.params.id,
						scoreType: 3
					}).then(d => {
						this.index2[3].evaluateList = d.pageInfo.list
					})
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.slideup-enter-active{
        animation-name: slideInUp;
        animation-duration: .2s;
        animation-fill-mode: both
    }
    .slideup-leave-active {
        animation-name: slideOutDown;
        animation-duration: .5s;
        animation-fill-mode: both
    }
    @keyframes slideInUp {
        0% {
            transform: translate3d(0,100%,0);
            visibility: visible
        }

        to {
            transform: translateZ(0)
        }
    }
    @keyframes slideOutDown {
        0% {
            transform: translateZ(0)
        }

        to {
            visibility: hidden;
            transform: translate3d(0,100%,0)
        }
    }
    .delay-leave-active{
        -webkit-animation-delay: .2s;
        -moz-animation-delay: .2s;
        -o-animation-delay: .2s;
        animation-delay: .2s;
    }
	
