		div:nth-child(5) {
			flex: 3;
			line-height: 50px;
			color: #fff;
			background: #ff1747;
		}
	}
	
	.detail-fixed {
		position: fixed;
		bottom: 70px;
		right: 20px;
		font-size: 12px;
		padding: 5px 10px;
		background: #fff;
		border: 1px solid #eee;
		border-radius: 5px;
	}
</style>


